html, body {
    height: 100vh;
    width: 100vw;
    margin: 0;
    padding: 0;
    font-family: "Arial", sans-serif;
    background: #000;
    color: #fff;
    display: flex;
    flex-direction: column;
    text-align: center;
    overflow: hidden;
    touch-action: none;
}

/* –ö–æ–Ω—Ç–µ–Ω—Ç —Å –≤–æ–∑–º–æ–∂–Ω–æ—Å—Ç—å—é —Å–∫—Ä–æ–ª–ª–∞ */
.scrollable-content {
    flex: 1;
    overflow-y: auto; /* üî• –í–∫–ª—é—á–∞–µ–º —Å–∫—Ä–æ–ª–ª–∏–Ω–≥ —Ç–æ–ª—å–∫–æ –∑–¥–µ—Å—å */
    -webkit-overflow-scrolling: touch; /* –ü–ª–∞–≤–Ω—ã–π —Å–∫—Ä–æ–ª–ª–∏–Ω–≥ –Ω–∞ iOS */
    padding: 20px;
}

/* –ù–∏–∂–Ω—è—è –ø–∞–Ω–µ–ª—å –Ω–∞–≤–∏–≥–∞—Ü–∏–∏ */
.bottom-nav {
    width: 100%;
    background: rgba(0, 0, 0, 0.9);
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 15px 0;
    position: fixed;
    bottom: 0;
    border-radius: 20px 20px 0 0;
}

/* –ì–ª–∞–≤–Ω—ã–π –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä */
.app {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    height: 100vh;
    width: 100%;
}

/* –í–µ—Ä—Ö–Ω—è—è –ø–∞–Ω–µ–ª—å —Å –ª–æ–≥–æ—Ç–∏–ø–æ–º */
.header {
    width: 100%;
    background: rgba(20, 20, 30, 0.95);
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px 0;
}

.logo {
    width: 150px;
}

/* –ö–æ–Ω—Ç–µ–Ω—Ç */
.content {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 24px;
    color: #fff;
    overflow-y: auto; /* üî• –û—Ç–∫–ª—é—á–∞–µ–º –æ—Å–Ω–æ–≤–Ω–æ–π —Å–∫—Ä–æ–ª–ª–∏–Ω–≥ */
}

/* –ù–∏–∂–Ω—è—è –ø–∞–Ω–µ–ª—å –Ω–∞–≤–∏–≥–∞—Ü–∏–∏ */
.bottom-nav {
    width: 100%;
    background: rgba(0, 0, 0, 0.9);
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 15px 0;
    position: fixed;
    bottom: 0;
    border-radius: 20px 20px 0 0;
}

/* –ö–Ω–æ–ø–∫–∏ –º–µ–Ω—é */
.bottom-nav button {
    background: none;
    border: none;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
    padding: 10px;
    border-radius: 50%;
    width: 60px;
    height: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
}

/* –°—Ç–∏–ª–∏ –¥–ª—è –∏–∫–æ–Ω–æ–∫ */
.button-icon {
    width: 32px;
    height: 32px;
    filter: brightness(0.7);
    transition: filter 0.3s ease-in-out, transform 0.2s ease-in-out;
}

/* –í—ã–¥–µ–ª–µ–Ω–Ω–∞—è –∫–Ω–æ–ø–∫–∞ */
.bottom-nav button.active .button-icon {
    filter: brightness(1);
    transform: scale(1.2);
}

/* –≠—Ñ—Ñ–µ–∫—Ç –Ω–∞–≤–µ–¥–µ–Ω–∏—è */
.bottom-nav button:hover .button-icon {
    transform: scale(1.1);
}